*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  line-height: 1.8;
  font-size: 1.6rem;
  font-family: "Lato", sans-serif;
}

h1,
h2,
h3 {
  font-weight: 300;
  text-align: center;
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  color: #000000;
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

.container {
  width: 95%;
  margin: 0 auto;
  max-width: 120rem;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.centered-content {
  max-width: 80rem;
}

.align-rigth {
  display: flex;
  justify-content: flex-end;
}

.btn-yellow {
  display: block;
  color: #FFFFFF;
  padding: 1rem 3rem;
  text-align: center;
  font-weight: 700;
  background-color: #E08709;
  margin-top: 2.5rem;
  border: none;
}
.btn-yellow:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-yellow-block {
  display: inline-block;
  color: #FFFFFF;
  padding: 1rem 3rem;
  text-align: center;
  font-weight: 700;
  background-color: #E08709;
  margin-top: 2.5rem;
  border: none;
}
.btn-yellow-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-green {
  display: inline-block;
  color: #FFFFFF;
  padding: 1rem 3rem;
  text-align: center;
  font-weight: 700;
  background-color: #71B100;
  margin-top: 2.5rem;
  border: none;
}
.btn-green:hover {
  background-color: #619800;
  cursor: pointer;
}

.no-webp .inicio {
  background-image: url("../assets/img/header.jpg");
}

.webp .inicio {
  background-image: url("../assets/img/header.webp");
}

.header {
  padding: 1rem 0 3rem 0;
  background-color: #333333;
}
.header.inicio {
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .container-header {
    min-height: 70rem;
  }
}

.container-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.container-header h1 {
  line-height: 2;
  color: #FFFFFF;
  text-align: left;
  max-width: 60rem;
  font-weight: 700;
}

.footer {
  margin-bottom: 0;
  padding: 2.5rem 0;
  background-color: #333333;
}
.footer .contianer-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.bar {
  display: flex;
  padding-top: 3rem;
  flex-direction: column;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .navegation {
    display: flex;
    align-items: center;
  }
}
.navegation a {
  display: block;
  color: #FFFFFF;
  font-size: 2.2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .navegation a {
    margin-right: 1.8rem;
    font-size: 1.8rem;
  }
  .navegation a:last-of-type {
    margin-right: 0;
  }
}
.navegation a:hover {
  color: #c77808;
}

@media (min-width: 768px) {
  .icons-about {
    gap: 3rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon {
  text-align: justify;
  padding: 3rem 0 2rem 0;
  border-bottom: 0.3rem solid #d4d4d4;
}
.icon:last-of-type {
  border-bottom: none;
}
@media (min-width: 768px) {
  .icon {
    padding: 0;
    border-bottom: none;
  }
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .container-adverts {
    gap: 2rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.advert {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem;
}
.advert .content-advert {
  padding: 2rem;
}
.advert h3, .advert p {
  margin: 0;
}

.price {
  color: #71B100;
  font-size: 3rem;
  font-weight: 700;
}

.icons-features {
  gap: 2rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 0;
}
@media (min-width: 992px) {
  .icons-features {
    display: flex;
    max-width: 40rem;
    height: 3rem;
    list-style: none;
  }
}
.icons-features li {
  flex: 1;
  display: flex;
  align-items: center;
}
.icons-features li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.icons-features li p {
  font-weight: 900;
}

.no-webp .img-contact {
  background-image: url("../assets/img/encuentra.jpg");
}

.webp .img-contact {
  background-image: url("../assets/img/encuentra.webp");
}

.img-contact {
  padding: 5rem 1rem;
  background-size: cover;
  background-position: center center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.img-contact h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.img-contact p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .entrance-area {
    display: grid;
    column-gap: 2rem;
    grid-template-columns: 2fr 1fr;
  }
}
.blog-post {
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .blog-post {
    display: grid;
    column-gap: 2rem;
    grid-template-columns: 1fr 2fr;
  }
}
.blog-post:last-of-type {
  margin-bottom: 0;
}

.text-post a {
  color: #000000;
}
.text-post h4 {
  margin: 0;
  margin-top: 2rem;
  line-height: 1.4;
}
@media (min-width: 768px) {
  .text-post h4 {
    margin-top: 0;
  }
}
.text-post h4::after {
  content: "";
  width: 15rem;
  height: 0.5rem;
  display: block;
  margin-top: 1rem;
  background-color: #71B100;
}
.text-post span {
  color: #E08709;
  font-weight: 700;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  position: relative;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
}
.testimonial blockquote::before {
  position: absolute;
  content: "";
  background-image: url("../assets/img/comilla.svg");
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  left: 4rem;
  display: block;
}
.testimonial p {
  color: #FFFFFF;
  text-align: right;
}

@media (min-width: 768px) {
  .content-about-us {
    gap: 2rem;
    display: grid;
    grid-template-columns: repeat(2, 2fr);
  }
}

.text-about-us blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding-left: 1rem 0 3rem 0;
}

.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  display: block;
  font-weight: 700;
  text-transform: uppercase;
}
.form input:not([type=submit]),
.form select,
.form textarea {
  width: 100%;
  padding: 1rem;
  display: block;
  border-radius: 1rem;
  margin-bottom: 2rem;
  border: 1px solid #E1E1E1;
  background-color: #FFFFFF;
}
.form textarea {
  height: 20rem;
}
.form .form-contact {
  display: flex;
  max-width: 30rem;
  align-items: center;
  justify-content: space-between;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}